.dt_input {
  color: var(--color_inputText);
  flex-grow: 1;
  width: 100%;
  padding: 10px 15px;
  padding-left: 8px;
  margin-bottom: 5px;
  font-size: 16px;
  background-color: white;
  border-radius: 5px;
  border: 1px solid #b8b8b8;
  outline: none;
  transition: box-shadow ease 250ms;
  margin-top: 4px;
  background-color: var(--color_inputBackground);
  border-radius: 5px;
  border: 2px solid var(--color_b1);
  border-color:  var(--color_inputBackground);
}

.dt_input:focus {
  transition: border-color ease 250ms, background-color ease 250ms;
  border-color:  var(--color_inputFocusBorder);
  background-color: var(--color_inputFocusBackground);
  box-shadow: 0 0 7px rgba(255, 255, 255, 0.2);
}